@keyframes blink {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }
}

.typewriter {
  --caret: currentcolor;
}

.typewriter:before {
  content: "";
  animation: 13.5s infinite typing;
}

.typewriter:after {
  content: "";
  border-right: 2px solid var(--caret);
  animation: .5s linear infinite blink;
}

.typewriter.thick:after {
  border-right: 1ch solid var(--caret);
}

.typewriter.nocaret:after {
  border-right: 0;
}

@media (prefers-reduced-motion) {
  .typewriter:after {
    animation: none;
  }

  @keyframes sequencePopup {
    0%, 100% {
      content: "Web";
    }

    25% {
      content: "Android";
    }

    50% {
      content: "Blockchain";
    }
  }

  .typewriter:before {
    content: "Web";
    animation: 12s linear infinite sequencePopup;
  }
}

.gradient {
  color: #0000;
  background: linear-gradient(90deg, #edede8, #8adfe5, #edede8) 0 0 / 200% 100% text;
  animation: 8s linear infinite move-bg;
}

@keyframes move-bg {
  to {
    background-position: 400% 0;
  }
}

